@use 'base/mixins' as m;
@use 'base/variables' as v;


.home {
    margin-top: 5rem;
    .uptask {
        @include m.textoDegradado(v.$cyan);
        animation: escalar 1s infinite alternate;
    }
    .tagline{
        margin-top: 4rem;
    }

}
 
 @keyframes escalar {
    from {
      transform: scale(1); /* Escala inicial al 100% */
    }
    to {
      transform: scale(1.1); /* Escala final al 120% */
    }
  }